"use client";
import { useState } from "react";
import { UploadButton } from "../../utils/uploadthing";

type UploadResult = {
  name: string;
  url: string;
  key: string;
};

interface ImageUploadProps {
  onUploadComplete: (data: UploadResult) => void;
}

export default function ImageUpload({ onUploadComplete }: ImageUploadProps) {
  const [previewUrl, setPreviewUrl] = useState("");

  return (
    <div className="flex flex-col gap-4 items-center">
      <UploadButton
        endpoint="imageUploader"
        onClientUploadComplete={(res) => {
          const uploaded = res[0];
          const result = {
            name: uploaded.name,
            url: uploaded.url,
            key: uploaded.key,
          };

          setPreviewUrl(uploaded.url);
          onUploadComplete(result);
        }}
        onUploadError={(error) => {
          alert(`Upload Error: ${error.message}`);
        }}
        appearance={{
          button: "bg-indigo-600 hover:bg-indigo-700 text-white font-semibold px-4 py-2 rounded shadow",
          container: "flex justify-center",
        }}
      />

      {previewUrl && (
        <div className="w-48 h-48 rounded overflow-hidden border border-gray-300 shadow">
          <img
            src={previewUrl}
            alt="Uploaded Logo"
            className="w-full h-full object-cover"
          />
        </div>
      )}
    </div>
  );
}
